var ding1 = new Audio(process.env.PUBLIC_URL + '/singleDing.mp3');
var ding2 = new Audio(process.env.PUBLIC_URL + '/doubleDing.mp3');

function singleDing(){
    ding1.play();
}

function doubleDing(){
    ding2.play();
}

export {
    singleDing,
    doubleDing
};


